<script>
// @ is an alias to /src
import NavBar from "@/components/NavBar.vue";
import SparkLine from "@/components/SparkLine.vue";
import Pie from "@/components/Pie.vue";
import Ccircle from "@/components/Ccircle.vue";

export default {
  name: "more",
  components: {
    NavBar,
    SparkLine,
    Pie,
    Ccircle
  },

  data() {
    return {
      currentRate: 0
    };
  },
  computed: {
    text() {
      let vm = this;
      setTimeout(function(){
        vm.currentRate = vm.currentRate - 20;

        console.log(vm.currentRate)
      },2000)
      return vm.currentRate.toFixed(0) + '%'
    }
  },

  methods: {
    // add contact
    onAdd() {
      this.editingContact = { id: this.list.length };
      this.isEdit = false;
      this.showEdit = true;
    },

    // edit contact
    onEdit(item) {
      this.isEdit = true;      
      this.showEdit = true;
      this.editingContact = item;
    },

    // select contact
    onSelect() {
      this.showList = false;
    },

    // save contact
    onSave(info) {
      this.showEdit = false;
      this.showList = false;
      
      if (this.isEdit) {
        this.list = this.list.map(item => item.id === info.id ? info : item);
      } else {
        this.list.push(info);
      }
      this.chosenContactId = info.id;
    },

    // delete contact
    onDelete(info) {
      this.showEdit = false;
      this.list = this.list.filter(item => item.id !== info.id);
      if (this.chosenContactId === info.id) {
        this.chosenContactId = null;
      }
    },

    onChange(index) {
      this.showList = false;
      this.chosenCoupon = index;
    },
    onExchange(code) {
      this.coupons.push(coupon);
    }

  }
};
