      let vm = this;

      let obj = {}

      if(vm.categories && vm.categories.length){
        obj = {
          chart: {
            id: 'vuechart',
            width: '100%',
            toolbar: {
              show: true,
              tools: {
                download: true,
                // selection: false,
                // zoom: false,
                // zoomin: false,
                // zoomout: false,
                // pan: false
              }
            },
          },
          colors: vm.colors || ['#2E93fA', '#66DA26', '#546E7A', '#E91E63', '#FF9800'],
          labels: vm.categories,
          legend: {
            show: true,
            position: 'left',
            offsetY: 20,
          }
          // xaxis: {
          //   // labels: {
          //   //   show: false,
          //   // },
          //   categories: vm.categories
          // },
          // yaxis: {
          //   labels: {
          //     show: false,
          //   },
          // },
          // grid: {
          //   yaxis: {
          //     lines: {
          //       show: false,
          //     },
          //   }
          // },
          // tooltip: {
          //   theme: 'dark'
          // }
        }
      }

      return obj
    },
    data(){
      let vm = this;
      let arr = [];
      if(vm.series && vm.series.length > 0){
        arr = vm.series
      }
      return arr
    }
  },
};
</script>
