<template>
  <div class="c-wrap" v-if="num">
    <h2>
      {{title}}
      <span>{{content}}</span>
    </h2>
    
    <van-circle
      v-model="value"
      :color="color"
      :rate="rate"
      :speed="36"
      :text="text"
      :size="120"
    />
  </div>
</template>

<script>
export default {
  name: 'vl',
  props: ["color","title","content","num","sum"],
  data(){
    return{
      value: 0
    }
  },
  computed: {
    text(){
      let vm = this;
      return Number((100 * vm.num/vm.sum).toFixed(1)) + '%'
    },
    rate() {
      let vm = this;
      if(vm.num && vm.sum) {
        return Number((100 * vm.num/vm.sum).toFixed(0))
      }
      return 100
    }
  }

};
</script>
<style lang="scss">
.c-wrap{
  position: relative;
  text-align: center;
  overflow: hidden;
  padding: 1rem;
  margin-bottom: 1rem;
}
.c-wrap h2{
  font-size: 15pt;
}
.c-wrap h2 > span{
  display: block;
  font-size: 10pt;
}
</style>